<template>
  <div class="toasts">
    <!--加载中-->
    <div class="toast toast_loading" v-if="toast.type === 0 && toast.state">
      <div class="toast__mask"></div>
      <div class="toast__body">
        <i class="toast__icon toast__icon_loading"></i>
        <p class="toast__content" v-html="toast.text"></p>
      </div>
    </div>
    <!--完成-->
    <div class="toast toast_success" v-if="toast.type === 1 && toast.state">
      <div class="toast__mask"></div>
      <div class="toast__body">
        <i class="toast__icon toast__icon_success"></i>
        <p class="toast__content" v-html="toast.text"></p>
      </div>
    </div>
    <!--失败-->
    <div class="toast toast_failed" v-if="toast.type === 2 && toast.state">
      <div class="toast__mask"></div>
      <div class="toast__body">
        <i class="toast__icon toast__icon_failed"></i>
        <p class="toast__content" v-html="toast.text"></p>
      </div>
    </div>
    <!--警告-->
    <div class="toast toast_waring" v-if="toast.type === 3 && toast.state">
      <div class="toast__mask"></div>
      <div class="toast__body">
        <i class="toast__icon toast__icon_waring"></i>
        <p class="toast__content" v-html="toast.text"></p>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
export default{
  props: {
    toast: {
      type: Object,
      default () {
        return {
          type: 0, //  提示类型： 0 加载中 1 完成 2 失败 3 警告
          state: 0, //  提示状态 0 不显示 1 显示
          text: '数据加载中' //  显示文字
        }
      }
    }
  }
}
</script>

<style lang="scss" rel="stylesheet/scss">
  @import '../../scss/fn.scss';

  .toast {
    &__mask {
      position: fixed;
      z-index: 100;
      top: 0;
      right: 0;
      left: 0;
      bottom: 0;
    }
    &__body {
      position: fixed;
      z-index: 110;
      width: 10em;
      min-height: 8em;
      top: 180px;
      left: 50%;
      margin-left: -5em;
      background: rgba(17, 17, 17, 0.7);
      text-align: center;
      border-radius: 5px;
      color: #FFFFFF;
    }
    &__icon{
      margin-top: 15px;
      margin-bottom: 5px;
      width: 38px;
      height: 38px;
      vertical-align: baseline;
      display: inline-block;
    }
    &__icon_success {
      background: url(./ico-success.png) no-repeat;
      background-size: 100% 100%;
    }
    &__icon_failed {
      background: url(./ico-failed.png) no-repeat;
      background-size: 100% 100%;
    }
    &__icon_waring {
      background: url(./ico-waring.png) no-repeat;
      background-size: 100% 100%;
    }
    &__icon_loading {
      animation: loading 1s steps(12, end) infinite;
      background: transparent url(data:image/svg+xml;base64,) no-repeat;
      background-size: 100% 100%;
    }
    &__content {
      margin: 0 0 15px;
    }
  }

  @keyframes loading {
    0% {
      transform: rotate3d(0, 0, 1, 0deg);
    }

    100% {
      transform: rotate3d(0, 0, 1, 360deg);
    }
  }
</style>



