<template>
  <div class="page-feedback">
    <div class="trees"></div>
    <div class="content">
      <div class="info">
        <ul class="form">
          <li class="form__item">
            <div class="form__name"><i class="icon icon_name"></i><i class="space">姓</i>名：</div>
            <div class="form__inputbox">
              <input class="form__input" type="text" v-model="feedback.name" ref="username">
            </div>
          </li>
          <li class="form__item">
            <div class="form__name"><i class="icon icon_tel"></i>联系方式：</div>
            <div class="form__inputbox">
              <input class="form__input" type="tel" v-model="feedback.tel" ref="tel">
            </div>
          </li>
          <li class="form__item form__item_flex-start">
            <div class="form__name"><i class="icon icon_txt"></i>反馈意见：</div>
            <div class="form__inputbox">
              <textarea id="text" placeholder="请详细描述您的问题或建议，我们将及时跟进解决。（不少于10个字）" v-model="feedback.content"
                        ref="textarea"></textarea>
            </div>
          </li>
          <li class="form__item">
            <div class="form__name">&emsp;</div>
            <div class="form__inputbox">
              <div class="btn" @click="submit"></div>
            </div>
          </li>
        </ul>

      </div>
    </div>
    <div class="footer">
      <img class="footer__img" src="../../../static/images/more.png" alt="更多" @click="showNav">
    </div>
    <div class="dialog" v-show="dialog.state">
      <div class="dialog__wrap">
        <div class="dialog__content">
          <div class="dialog__head">
            <img class="dialog__img" src="../../../static/images/dialog-tree.png" alt="图标">
          </div>
          <div class="dialog__list">
            <a class="dialog__item" v-for="link in dialog.links" :href="link.type == 1?link.content:'/page/'+link.id">
              <img class="dialog__icon" :src="host + link.pic" alt="icon">
              <p class="dialog__name">{{link.iconname}}</p>
            </a>
          </div>
          <a class="dialog__btn" :href="treeUrl">返回苗木信息</a>
        </div>
        <div class="dialog__close">
          <img src="../../../static/images/close.png" alt="close" @click="closeDialog">
        </div>
      </div>
    </div>
    <toast-box :toast="toast"></toast-box>
  </div>
</template>
<script type="text/ecmascript-6">
  import axios from 'axios'
  //  import qs from 'qs'
  import toastBox from '../../components/common/toast'
  import {api} from '../../assets/js/global'

  export default {
    name: 'app',
    components: {
      toastBox
    },
    data () {
      return {
        treeUrl: '', // 苗木地址
        toast: {type: 1, state: 0, text: ''},
        dialog: {state: 0, links: {}}, //  导航
        feedback: {
          name: '',
          tel: '',
          content: ''
        }
      }
    },
    methods: {
      closeToast () {
        let vm = this
        setTimeout(function () {
          vm.toast.state = 0
        }, 1500)
      },
      showNav () {
        this.dialog.state = 1
      },
      closeDialog () {
        this.dialog.state = 0
      },
      submit () {
        if (this.feedback.name === '') {
          this.toast = {type: 3, state: 1, text: '请填写姓名'}
          this.closeToast()
          this.$refs.username.focus()
          return false
        }
        let regexp = /^1[3456789]\d{9}$/
        if (this.feedback.tel === '' || !regexp.test(this.feedback.tel)) {
          this.toast = {type: 3, state: 1, text: '请填写正确号码'}
          this.closeToast()
          this.$refs.tel.focus()
          return false
        }
        if (this.feedback.content.length < 10) {
          this.toast = {type: 3, state: 1, text: '不少于10个字'}
          this.closeToast()
          this.$refs.textarea.focus()
          return false
        }
        let vm = this
        this.toast = {type: 0, state: 1, text: '提交中...'}
        axios.post(api + 'feedbackAdd', vm.feedback)
          .then(function (res) {
            console.log(res.data)
            if (res.data.code === 200) {
              vm.toast = {type: 1, state: 1, text: '提交成功'}
              vm.closeToast()
              vm.feedback = {
                name: '',
                tel: '',
                content: ''
              }
            } else {
              vm.toast = {type: 2, state: 1, text: '提交失败,请重试'}
              vm.closeToast()
            }
          })
          .catch(function (error) {
            vm.toast = {type: 2, state: 1, text: '提交失败,请重试'}
            vm.closeToast()
            console.log(error)
          })
      }
    },
    created () {
      this.treeUrl = window.localStorage.getItem('treeUrl')
    }
  }
</script>
<style lang="scss" rel="stylesheet/scss">
  @import '../../scss/fn.scss';

  html, body, .page-feedback {
    height: 100%;
    overflow: hidden;
  }

  body {
    background: url("../../../static/images/bg2.jpg") top center no-repeat;
    background-size: cover;
  }

  .page-feedback {
    position: relative;
    padding-top: tr(152);
    display: flex;
    flex-direction: column;
  }

  .trees {
    position: absolute;
    width: tr(375);
    height: tr(182);
    top: 0;
    background: url("../../../static/images/trees.png") top center no-repeat;
    background-size: 100% 100%;
    z-index: 100;
  }

  .space {
    letter-spacing: 2em;
  }

  .content {
    flex: 1;
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
  }

  .footer {
    height: tr(65);
    padding-top: tr(6);
    text-align: center;
    &__img {
      height: tr(38);
      width: tr(186);
    }
  }

  .form {
    padding: 15px tr(69) 0 tr(36);
    &__item {
      width: 100%;
      margin-top: tr(13);
      display: flex;
      align-items: center;
      &_flex-start {
        align-items: flex-start;
      }
    }
    &__name {
      color: #494949;
      width: 7.4em;
    }
    &__inputbox {
      flex: 1;
    }
    &__input {
      width: 100%;
      padding: 6px 10px;
      -webkit-appearance: none;
      border-radius: 5px;
      border: 1px solid #bcbebd;
      background-color: #fff;
      outline: none;
    }
  }

  .icon {
    display: inline-block;
    width: 19px;
    height: 19px;
    vertical-align: middle;
    margin-right: tr(8);
    background: url("../../../static/images/feedback-icon.png") no-repeat center center;
    background-size: 57px 19px;
    &_name {
      background-position: 0 0;
    }
    &_tel {
      background-position: -19px 0;
    }
    &_txt {
      background-position: -38px 0;
    }
  }

  #text {
    width: 100%;
    height: tr(132);
    -webkit-appearance: none;
    border-radius: 5px;
    border: 1px solid #bcbebd;
    background-color: #fff;
    padding: 10px;
    resize: none;
    outline: none;
  }

  .btn {
    width: tr(117);
    height: tr(30);
    background: url("../../../static/images/submit-btn.png") no-repeat;
    background-size: 100% 100%;
  }
  .dialog {
    position: fixed;
    z-index: 100;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background: rgba(0, 0, 0, .7);
    display: flex;
    justify-content: center;
    align-items: center;
    &__wrap {
      position: relative;
    }
    &__content {
      position: relative;
      width: tr(225);
      min-height: tr(206);
      padding-top: tr(85);
      display: flex;
      flex-direction: column;
      background-color: #fff;
      z-index: 110;
      border: 1px solid rgb(187, 187, 187);
      border-radius: 5px;
      box-shadow: 0 0 15px 0 rgba(13, 4, 8, .33);
    }
    &__head {
      position: absolute;
      z-index: 200;
      top: - tr(70);
      left: 50%;
      margin-left: - tr(45);
    }
    &__img {
      width: tr(90);
      height: tr(140);
    }
    &__list {
      flex: 1;
      font-size: 0;
      display: flex;
      flex-wrap: wrap;
      padding: 0 tr(10) tr(22);
    }
    &__item {
      width: 33.3333%;
      display: inline-block;
      text-align: center;
      color: #407007;
      margin-bottom: tr(13);
    }
    &__icon {
      width: tr(43);
      height: tr(43);
    }
    &__name {
      font-size: 10px;
      font-weight: 600;
      margin-top: tr(5);
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
    &__btn {
      display: inline-block;
      width: 100%;
      color: #434343;
      text-align: center;
      font-size: 12px;
      line-height: 28px;
      border-top: 1px solid rgb(187, 187, 187);
      border-radius: 5px;
      box-shadow: 0 0 15px 0 rgba(13, 4, 8, 0.33);
    }
    &__close {
      position: absolute;
      right: - tr(32);
      bottom: tr(32);
      img {
        width: tr(32);
        height: tr(32);
      }
    }
  }
</style>



